@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --primary-color: #04458f;
  --secondary-color: #15032b;
  --dark-color: #333333;
  --light-color: #f4f4f4;
  --blue-color: #007cc7;
  --white-color: #eefbfb;
  --danger-color: #de354c;
  --success-color: #03a032;
  --yellow-color: #f1b24a;
  --other-color: #4f4846;
  --bg-color: #f2f2f2;
}

body {
  font-family: "Lato";
  font-size: 16;
  color: #333;
  background-color: var(--bg-color);
}

a {
  text-decoration: none;
}

/* Side Bar */
.side-bar {
  background-color: #15032b;
  background-image: linear-gradient(315deg, #2a2a72 20%, #011933 74%);
  width: 20%;
  height: 100vh;
  position: fixed;
  padding: 1rem 0;
  font-size: 1rem;
}

.side-bar .item {
  line-height: 1.6;
  margin-top: 25px;
}

ul {
  list-style: none;
}

.lis {
  text-decoration: none;
  margin-bottom: 15px;
  border-bottom: 1px solid #ccc;
}

.item a,
.lis h4 {
  color: #ccc;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 0.78rem;
}

.item h4 {
  padding: 0.3rem 1rem;
}

.item li {
  padding-bottom: 0.8rem;
  padding-top: 0.8rem;
}

.arrows {
  float: right;
  font-size: 0.78rem;
  color: #ccc;
  cursor: pointer;
  padding-top: 4px;
}

.ul-child {
  color: white;
  cursor: pointer;
  transition: height 1s ease;
}

.hidden {
  display: none;
}

.ul-child li {
  padding: 0.3rem 1rem;
  padding-left: 2.8rem !important;
}

.ul-child a {
  text-transform: capitalize;
  font-weight: 600;
  color: white;
}

.ul-child li:hover {
  background-color: var(--secondary-color);
}

.fa-big {
  font-size: 18px;
  padding-right: 10px;
}

/* Header */
.header {
  background-color: white;
  height: 70px;
  border-bottom: 1px solid #ccc;
  width: 100%;
  padding: 15px;
}

.remove-backs {
  background-color: white;
  outline: none;
  border: none;
}

.sbtn {
  background-color: transparent;
  padding: 0.5rem 1rem;
  outline: none;
  font-size: 0.85rem;
  border-radius: 5px;
  background-color: var(--yellow-color);
  border: none;
  cursor: pointer;
}

.cursor-r {
  cursor: pointer;
  padding: 10px;
}

.header-flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-input {
  flex: 1.5;
}

.not-parent {
  flex: 1;
}
.notification {
  display: flex;
  justify-content: space-evenly;
  align-items: "center";
}

.avatar {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f2f2f2;
  border: 1px solid #ccc;
}

.avatar .fa {
  font-size: 25px;
}

.img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

/* Utility */
.container-wrapper {
  padding-left: 20%;
}
.float {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.float-right {
  float: right;
  margin-top: 10px;
}

.float p {
  margin: 0 5px;
  font-size: 0.8rem;
  text-decoration: underline;
}

.float p:hover {
  color: darkred;
  font-weight: 600;
  cursor: pointer;
}
select,
input[type="text"],
input[type="email"],
input[type="date"],
input[type="Number"],
input[type="email"],
input[type="file"],
input[type="password"],
textarea {
  padding: 0.3rem;
  width: 100%;
  outline: none;
  margin-bottom: 5px;
  color: grey;
}

.short {
  padding: 0.5rem;
  width: 48% !important;
  outline: none;
  margin-bottom: 10px;
}

.short1 {
  padding: 0.5rem;
  width: 48% !important;
  margin-left: 1.4rem;
  outline: none;
  margin-bottom: 10px;
}

.customize-short {
  border-radius: 5px;
  margin-right: 10px;
  padding: 0.5rem !important;
  border-color: #f2f2f2;
}

.btn {
  cursor: pointer;
  font-size: 0.8rem;
  height: 2rem;
  border: none;
  border-radius: 10px;
  background-color: var(--primary-color);
  padding: 0.5rem 1.8rem;
  color: #fff;
  outline: none;
  box-shadow: 0 0.1rem rgba(131, 140, 230, 0.65);
}

.btn:hover {
  filter: brightness(110%);
}

.btn:active {
  transform: translate(0, 0.3rem);
  box-shadow: 0 0.1rem rgba(225, 225, 225, 0.65);
}

table {
  border-collapse: collapse;
  width: 100%;
}

table tr th {
  text-align: left;
}

.th-wide {
  width: 130px;
  text-align: left;
}

.stx {
  background-color: transparent;
  padding: 0.3rem;
  outline: none;
  font-size: 0.5rem;
  font-weight: 600;
  border-radius: 5px;
  background-color: var(--yellow-color);
  outline: none;
  border: none;
  cursor: pointer;
}

.sbtn:hover,
.stx:hover {
  transform: scale(0.99);
  filter: brightness(110%);
}

.success {
  color: #03a032;
}

.danger {
  color: red;
}

.white-back {
  background-color: white;
  border: 1px solid #ccc;
  padding: 15px;
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-around;
  align-items: center;
  grid-gap: 0 5rem;
  margin: 15px;
}

.absolutes {
  position: fixed;
  width: 100%;
  background-color: rgba(139, 139, 139, 0.8);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000000;
  top: 0;
}

.img-views {
  width: 800px;
  height: 600px;
  background-color: white;
  padding: 10px;
}

.img-g {
  width: 780px;
  height: 580px;
}

.close {
  background-color: darkred;
  color: white;
  padding: 5px 10px;
  position: absolute;
  right: 40px;
}

.close:hover {
  background-color: red;
  cursor: pointer;
}

.btn-spinner {
  box-sizing: border-box;
  height: 15px;
  width: 15px;
  border-radius: 50%;
  border: 2px solid transparent;
  border-top-color: white;
  border-bottom-color: white;
  animation: btx-spin 0.7s ease infinite;
}

@keyframes btx-spin {
  to {
    transform: rotate(360deg);
  }
}

.alert {
  border-radius: 20px;
  width: 40%;
  height: 40%;
  background-color: white;
  /* border: 1px solid #333; */
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

/* DragNDrop */
.grid-center {
  display: grid;
  justify-content: center;
  align-items: center;
  z-index: 10000000;
  height: 70vh;
}

.drag-n-drop {
  background-color: white;
  padding: 60px;
  border: 1px solid #ccc;
}

.highlighted {
  border: 1px solid greenyellow;
  font-size: 1.5rem;
}

/* New-Capture */
.new-capture {
  padding: 15px;
}

.new-capture th {
  font-size: 0.7rem;
  padding: 0.5rem;
  background-color: #04458f;
  color: white;
  border: 1px solid #ccc;
}

.new-capture td {
  font-size: 0.7rem;
  padding: 0.2rem 0.5rem;
  color: #333;
  background-color: white;
  border-bottom: 1px solid #f2f2f2;
  font-size: 0.65rem;
  font-weight: 600;
}

/* Alerts */
.card {
  border: dotted 1px #333;
  margin-top: 25px;
  padding: 1rem 2rem;
  width: 40rem;
  background-color: white;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.cancel-parent {
  width: 100%;
  padding: 0.1rem;
  display: flex;
  justify-content: space-between;
}
.cancel {
  background-color: red;
  color: white;
  font-weight: 600;
  padding: 0.4rem 0.8rem;
  cursor: pointer;
}

.sm {
  font-size: 0.9rem;
}

.cancel:hover {
  filter: brightness(110%);
  background-color: darkred;
}

.spinner {
  box-sizing: border-box;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  border: 2px solid transparent;
  border-top-color: white;
  border-bottom-color: white;
  animation: spinner 0.7s ease infinite;
}

@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}
